.queue-container {
    width: 90%;
    height: 35%;
    /* border-radius: 30px;
    border-top-right-radius: 0px; */
    background-color: #6a3ed23f;
    opacity: 1;
    /* background-image: repeating-radial-gradient(
        circle at 0 0,
        transparent 0,
        #833ed287 40px
      ),
      repeating-linear-gradient(#4767d055, #7747d0a2); */
      border-style: groove;
    border-color: black;
    border-radius: 30px;
    background: #0000008f;
    border-width: 3px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    bottom: 1%;
  }
  
  .queue {
    height: 85%;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
  }
  
  .upNext {
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-align: left;
    margin: 10px 0px;
  }
  
  .queue-list {
    height: 80%;
    width: 100%;
    overflow-y: auto;
  }
  
  .queue-list::-webkit-scrollbar {
    display: none;
  }
  
  .queue-item {
    justify-content: space-between;
    width: 100%;
    padding: 5px 0px;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .queue-item:hover {
    transform: scale(0.95);
  }
  
  .queue-item p {
    margin: 0px;
  }
  
  .track-name {
    width: 75%;
    text-overflow: ellipsis;
  }
  